.studio {
  background: linear-gradient(135deg, #0a0a0f 0%, #16213e 50%, #0a0a0f 100%);
  min-height: calc(100vh - 80px);
  color: #fff;
  padding: 2rem;
}

.studio-container {
  max-width: 1400px;
  margin: 0 auto;
}

/* Studio Header */
.studio-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: linear-gradient(135deg, rgba(26, 26, 46, 0.6) 0%, rgba(15, 52, 96, 0.4) 100%);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 12px;
}

.header-left {
  display: flex;
  align-items: center;
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.4rem 1rem;
  background: rgba(0, 255, 136, 0.1);
  border: 1px solid rgba(0, 255, 136, 0.3);
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  letter-spacing: 1px;
  font-family: 'Play', 'Courier New', sans-serif;
}

.status-dot {
  width: 8px;
  height: 8px;
  background: #00ff88;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 255, 136, 0.8);
  animation: pulse 2s ease-in-out infinite;
}

.status-text {
  color: #00ff88;
}

.header-right {
  display: flex;
  gap: 1rem;
}

.music-heart {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  padding: 1rem;
  background: rgba(255, 0, 85, 0.1);
  border: 1px solid rgba(255, 0, 85, 0.3);
  border-radius: 50%;
}

.heart-icon {
  font-size: 2.5rem;
  color: #ff0055;
  animation: heartbeat 1.5s ease-in-out infinite;
  text-shadow: 0 0 20px rgba(255, 0, 85, 0.6);
  z-index: 1;
}

@keyframes heartbeat {
  0%, 100% {
    transform: scale(1);
  }
  10% {
    transform: scale(1.2);
  }
  20% {
    transform: scale(1);
  }
}

.music-notes {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
}

.note {
  position: absolute;
  font-size: 1.2rem;
  color: #00d4ff;
  opacity: 0;
  animation: floatNote 3s ease-in-out infinite;
}

.note-1 {
  left: 20%;
  top: 10%;
  animation-delay: 0s;
}

.note-2 {
  right: 15%;
  top: 20%;
  animation-delay: 1s;
}

.note-3 {
  left: 15%;
  bottom: 20%;
  animation-delay: 2s;
}

@keyframes floatNote {
  0% {
    transform: translateY(0) scale(0.5);
    opacity: 0;
  }
  50% {
    transform: translateY(-20px) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateY(-40px) scale(0.5);
    opacity: 0;
  }
}

/* Workspace */
.studio-workspace {
  display: grid;
  grid-template-columns: 1fr 300px;
  gap: 2rem;
  margin-bottom: 2rem;
}

/* Track Channels */
.track-channels {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.track-channel {
  background: linear-gradient(135deg, rgba(26, 26, 46, 0.8) 0%, rgba(15, 52, 96, 0.6) 100%);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s ease;
}

.track-channel.active {
  border-color: #00d4ff;
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.2);
}

.track-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  background: rgba(15, 52, 96, 0.5);
  border-bottom: 1px solid rgba(0, 212, 255, 0.2);
}

.track-number {
  font-size: 1.2rem;
  font-weight: 700;
  color: #0099ff;
  font-family: 'Play', 'Courier New', sans-serif;
  width: 40px;
}

.track-name {
  flex: 1;
  font-size: 0.9rem;
  font-weight: 600;
  letter-spacing: 2px;
  color: #a8b2d1;
  font-family: 'Play', 'Courier New', sans-serif;
}

.track-status {
  font-size: 1.2rem;
  color: #0f3460;
  transition: all 0.3s ease;
}

.track-status.armed {
  color: #ff0055;
  text-shadow: 0 0 10px rgba(255, 0, 85, 0.8);
  animation: pulse 2s ease-in-out infinite;
}

.track-body {
  display: grid;
  grid-template-columns: 1fr 200px 60px;
  gap: 1.5rem;
  padding: 1.5rem;
}

.track-waveform {
  min-height: 100px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 8px;
  overflow: hidden;
}

.waveform-empty, .waveform-loading {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #0f3460;
  font-size: 0.85rem;
  font-family: 'Play', 'Courier New', sans-serif;
}

.waveform-loading {
  color: #00d4ff;
}

.waveform-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.waveform-display {
  flex: 1;
  height: 120px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 1px;
  padding: 0;
  background: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}

.waveform-display:hover {
  background: rgba(0, 0, 0, 0.6);
}

.waveform-bar {
  flex: 1;
  min-height: 3px;
  background: #0099ff;
  opacity: 0.8;
}

.waveform-canvas {
  flex: 1;
  width: 100%;
  height: 120px;
  border-radius: 4px;
  cursor: pointer;
  background: #1a1a2e;
  display: block;
}

.playhead {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 2px;
  background: #ff0055;
  box-shadow: 0 0 10px rgba(255, 0, 85, 0.8);
  pointer-events: none;
  z-index: 10;
}

.play-button {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: linear-gradient(135deg, #00d4ff 0%, #0099ff 100%);
  border: none;
  color: #0a0a0f;
  font-size: 1.2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 212, 255, 0.3);
}

.play-button:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 20px rgba(0, 212, 255, 0.5);
}

.track-controls {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.file-input-label {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.8rem 1.5rem;
  background: linear-gradient(135deg, #00d4ff 0%, #0099ff 100%);
  color: #0a0a0f;
  border-radius: 8px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Play', 'Courier New', sans-serif;
  box-shadow: 0 5px 15px rgba(0, 212, 255, 0.3);
}

.file-input-label:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 212, 255, 0.4);
}

.file-input-hidden {
  display: none;
}

.label-icon {
  font-size: 0.8rem;
}

.track-info {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.info-row {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.info-label {
  font-size: 0.7rem;
  color: #0099ff;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Play', 'Courier New', sans-serif;
}

.info-value {
  font-size: 0.85rem;
  color: #a8b2d1;
  word-break: break-all;
  font-family: 'Play', 'Courier New', sans-serif;
}

.track-meter {
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.meter-bar {
  width: 100%;
  background: linear-gradient(180deg, #ff0055 0%, #ffbd2e 50%, #00ff88 100%);
  transition: height 0.3s ease;
  box-shadow: 0 0 15px rgba(0, 212, 255, 0.5);
}

/* Master Control */
.master-control {
  background: linear-gradient(135deg, rgba(26, 26, 46, 0.8) 0%, rgba(15, 52, 96, 0.6) 100%);
  border: 2px solid rgba(0, 212, 255, 0.3);
  border-radius: 12px;
  overflow: hidden;
}

.master-header {
  padding: 1rem;
  background: rgba(15, 52, 96, 0.5);
  border-bottom: 1px solid rgba(0, 212, 255, 0.2);
  text-align: center;
}

.master-title {
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 3px;
  color: #00d4ff;
  font-family: 'Play', 'Courier New', sans-serif;
}

.master-body {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}

.process-button {
  width: 100%;
  padding: 1.2rem 2rem;
  background: linear-gradient(135deg, #00d4ff 0%, #0099ff 100%);
  color: #0a0a0f;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-family: 'Play', 'Courier New', sans-serif;
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.3);
}

.process-button:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 212, 255, 0.5);
}

.process-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.process-button.processing {
  background: linear-gradient(135deg, #ff0055 0%, #ffbd2e 100%);
  animation: pulse 1.5s ease-in-out infinite;
}

.button-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid #0a0a0f;
  border-top-color: transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.button-icon {
  font-size: 0.9rem;
}

.master-meter {
  width: 100%;
}

.meter-label {
  font-size: 0.7rem;
  color: #a8b2d1;
  text-align: center;
  margin-bottom: 0.5rem;
  letter-spacing: 1px;
  font-family: 'Play', 'Courier New', sans-serif;
}

.meter-track {
  height: 120px;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.meter-fill {
  width: 100%;
  height: 0%;
  background: linear-gradient(180deg, #ff0055 0%, #ffbd2e 50%, #00ff88 100%);
  transition: height 0.3s ease;
}

.meter-fill.animating {
  animation: meterPulse 1.5s ease-in-out infinite;
}

@keyframes meterPulse {
  0%, 100% {
    height: 30%;
  }
  50% {
    height: 90%;
  }
}

/* Output Console */
.output-console {
  background: linear-gradient(135deg, rgba(26, 26, 46, 0.9) 0%, rgba(15, 52, 96, 0.8) 100%);
  border: 1px solid rgba(0, 212, 255, 0.3);
  border-radius: 12px;
  overflow: hidden;
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.console-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  background: rgba(15, 52, 96, 0.5);
  border-bottom: 1px solid rgba(0, 212, 255, 0.2);
}

.console-title {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  font-size: 1rem;
  font-weight: 600;
  color: #00d4ff;
  font-family: 'Play', 'Courier New', sans-serif;
  letter-spacing: 1px;
}

.console-icon {
  font-size: 1.2rem;
}

.console-close {
  background: transparent;
  border: none;
  color: #a8b2d1;
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
}

.console-close:hover {
  background: rgba(255, 0, 85, 0.2);
  color: #ff0055;
}

.console-content {
  padding: 2rem;
}

.console-status {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 2px;
  font-family: 'Play', 'Courier New', sans-serif;
}

.status-icon {
  font-size: 1.5rem;
}

.status-icon.success {
  color: #00ff88;
  text-shadow: 0 0 10px rgba(0, 255, 136, 0.8);
}

.status-icon.error {
  color: #ff0055;
  text-shadow: 0 0 10px rgba(255, 0, 85, 0.8);
}

.console-error .console-status {
  color: #ff0055;
}

.console-success .console-status {
  color: #00ff88;
}

.console-message {
  color: #a8b2d1;
  font-size: 1rem;
  margin-bottom: 1.5rem;
  font-family: 'Play', 'Courier New', sans-serif;
}

.console-details {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.detail-section {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 8px;
  padding: 1.5rem;
}

.detail-title {
  font-size: 0.85rem;
  font-weight: 600;
  color: #00d4ff;
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Play', 'Courier New', sans-serif;
}

.detail-list {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.detail-item {
  display: flex;
  gap: 1rem;
  font-size: 0.9rem;
  font-family: 'Play', 'Courier New', sans-serif;
}

.item-label {
  color: #0099ff;
  font-weight: 600;
}

.item-value {
  color: #a8b2d1;
  word-break: break-all;
}

.detail-output {
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 4px;
  padding: 1rem;
  color: #00ff88;
  font-size: 0.85rem;
  font-family: 'Play', 'Courier New', sans-serif;
  overflow-x: auto;
  white-space: pre-wrap;
  word-break: break-word;
}

/* Responsive */
@media (max-width: 1024px) {
  .studio-workspace {
    grid-template-columns: 1fr;
  }

  .track-body {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
  }

  .track-meter {
    height: 60px;
  }

  .meter-bar {
    height: 100% !important;
  }
}

@media (max-width: 768px) {
  .studio {
    padding: 1rem;
  }

  .studio-header {
    flex-direction: column;
    gap: 1rem;
  }

  .header-left {
    flex-direction: column;
    gap: 1rem;
  }

  .studio-title {
    font-size: 1.4rem;
  }
}
